.stat-columns {
  @include reset-list;
  margin: unit(2) auto;
  max-width: width('medium');

  @include respond-to('small') {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.stat-columns__item {
  margin-top: unit(2);

  &:nth-child(1) {
    margin-top: 0;
  }

  @include respond-to('small') {
    width: calc(50% - #{unit(1)});

    &:nth-child(2) {
      margin-top: 0;
    }
  }

  @include respond-to('medium') {
    width: calc(50% - #{unit(2)});
  }

  @include respond-to('medium-large') {
    width: calc(50% - #{unit(3)});
  }
}
